<template>
  <div class="map-wrap">
    <app-header
      title="行车轨迹"
      showBack="true"
    ></app-header>
    <div class="map-main-wrap" id="wrap-main">
      <div class="date" id="info">
        <label>
          开始:
          <input type="date" v-model="startTime" style="width: 6rem">
        </label>
        <label>
          结束:
          <input type="date" v-model="endTime" style="width: 6rem">
        </label>
        <a href="javascript:void(0)" @click="searchTrack" class="btn">搜索</a>
        <div class="btn-wrap">
          <a class="start" href="javascript:void(0)" id="start">开始</a>
          <a class="end" href="javascript:void(0)" id="pause">暂停</a>
        </div>
      </div>
      <div class="wrap" id="Map">
        <div class="map" id="bMapWrap" style="height: 100%;"></div>
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
import AppHeader from '../../../../components/header/header.vue'
import {getTrackInfos} from '../../../../api/maintain'
import {initAMap} from '../../../../assets/js/amap'
import {setHeight} from '../../../../lib/util'
export default {
  name: 'Track-Map',
  data () {
    return {
      startTime: '',
      endTime: '',
      startAddress: '',
      endAddress: '',
      addressList: ''
    }
  },
  created () {
    initAMap()
  },
  mounted () {
    this.$nextTick(() => {
      document.getElementById('bMapWrap').style.height = (setHeight('wrap-main', 'info') - 55) + 'px'
    })
  },
  methods: {
    searchTrack () {
      this.$indicator.open({
        text: '加载中'
      })
      let _startTime = new Date(this.startTime).getTime() / 1000
      let _endTime = new Date(this.endTime).getTime() / 1000
      getTrackInfos(this.$route.params.id, _startTime, _endTime)
        .then(data => {
          this.$indicator.close()
          if (data.state) {
            this.$set(this, 'addressList', data.data.list)
            this.$set(this, 'startAddress', data.data.list[0])
            this.$set(this, 'endAddress', data.data.list[data.data.list.length - 1])
            this.createMap()
          } else {
            this.$Message.error(data.errormsg || '获取数据失败')
          }
        })
        .catch(err => console.log(err))
    },
    createMap () {
      var marker, lineArr = [];
      var map = new AMap.Map("Map", {
        resizeEnable: true,
        center: this.startAddress,
        zoom: 17
      });
      marker = new AMap.Marker({
        map: map,
        position: this.startAddress,
        icon: "http://webapi.amap.com/images/car.png",
        offset: new AMap.Pixel(-26, -13),
        autoRotation: true
      });

      let self = this
      // 设置终点和起点
      AMapUI.loadUI(['overlay/SimpleMarker'], function (SimpleMarker) {
        new SimpleMarker({
          iconLabel: {
            innerHTML: '起点',
            style: {
              color: '#fff',
              fontSize: '14px'
            }
          },
          iconStyle: 'green',
          map: map,
          position: self.startAddress
        })

        new SimpleMarker({
          iconLabel: {
            innerHTML: '终点',
            style: {
              color: '#fff',
              fontSize: '14px'
            }
          },
          iconStyle: 'red',
          map: map,
          position: self.endAddress
        })
      })

      lineArr = this.addressList
      // 绘制轨迹
      var polyline = new AMap.Polyline({
        map: map,
        path: lineArr,
        strokeColor: "#00A",  //线颜色
        // strokeOpacity: 1,     //线透明度
        strokeWeight: 3,      //线宽
        // strokeStyle: "solid"  //线样式
      });
      var passedPolyline = new AMap.Polyline({
        map: map,
        // path: lineArr,
        strokeColor: "#F00",  //线颜色
        // strokeOpacity: 1,     //线透明度
        strokeWeight: 3,      //线宽
        // strokeStyle: "solid"  //线样式
      });
      marker.on('moving',function(e){
        passedPolyline.setPath(e.passedPath);
      })
      map.setFitView();

      AMap.event.addDomListener(document.getElementById('start'), 'click', function() {
        marker.moveAlong(lineArr, 500);
      }, false);
      AMap.event.addDomListener(document.getElementById('pause'), 'click', function() {
        marker.pauseMove();
      }, false);
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .map-wrap{
    height: 100%;
    background-color: #fff;
    .map-main-wrap{
      width: 100%;
      height: 100%;
      position: fixed;
      padding-top: 40px;
      overflow: hidden;
      background-color: #fff;
      .date {
        font-size: 13px;
        .btn{
          vertical-align: middle;
          padding: .16rem 1rem;
          background-color: #4caf50;
          color: #fff;
        }
        .btn-wrap {
          position: absolute;
          bottom: 100px;
          width: 100%;
        }
        a.start {
          cursor: pointer;
          opacity: 0.75;
          text-align: center;
          line-height: 60px;
          background-color: #272822;
          width: 50%;
          height: 60px;
          position: absolute;
          z-index: 888;
          bottom: 0;
          color: #fff;
        }
        a.end{
          cursor: pointer;
          opacity: 0.75;
          text-align: center;
          line-height: 60px;
          background-color: #272822;
          width: 50%;
          height: 60px;
          position: absolute;
          z-index: 888;
          bottom: 0;
          color: #fff;
          right: 0;
        }
      }
    }
  }
</style>



