export const initAMap = function () {
  let aMapScript = document.createElement('script')
  let aMapCache = document.createElement('script')
  let mapUi = document.createElement('script')
  aMapScript.setAttribute('src', 'http://webapi.amap.com/maps?v=1.4.0&key=03e38808578f29e45817603e4cef1d56')
  aMapCache.setAttribute('src', 'http://cache.amap.com/lbs/static/addToolbar.js')
  mapUi.setAttribute('src', 'http://webapi.amap.com/ui/1.0/main.js?v=1.0.11')
  document.head.appendChild(aMapScript)
  document.head.appendChild(aMapCache)
  document.head.appendChild(mapUi)
}



